import React, { useEffect, useState } from "react";
import axios from "axios";

const doTranslation = async (input, languageCode, cancelToken) => {
  const url = `https://dictionary.yandex.net/api/v1/dicservice.json/lookup?key=dict.1.1.20231114T162817Z.536d5c85eee4c049.ab21a4508e3f5712f1b0e121970cd8822d22acf5&lang=ru-${languageCode}&text=${input}`
  try {

    const {data} = await axios.get(url, {
      q: input,
      target: languageCode
    },
    { cancelToken: cancelToken.token })
    
    return data.def[0].tr[0].text;
    } catch (err) {
    return "";
  }
};

export default ({ language, text }) => {
  const [translated, setTranslated] = useState("");

  useEffect(() => {
    if (!text) {
      return;
    }

    const cancelToken = axios.CancelToken.source();

    doTranslation(text, language, cancelToken).then(setTranslated);

    return () => {
      try {
        cancelToken.cancel();
      } catch (err) {}
    };
  }, [text, language]);

  return (
    <div>
      <label className="label">Output</label>
      <h1 className="title">{translated}</h1>
    </div>
  );
};
